<template>
  <div class="" id="current_positions_section">
    <div class="main_heading">
      Current positions
    </div>
    <div
      class="flow_section"
      v-for="index in number_of_flow_section"
      :key="index"
    >
      <div class="left_section">
        <div class="title">
          {{
            left_section_item(index) !== null
              ? left_section_item(index).title
              : ""
          }}
        </div>
        <div class="location">
          {{
            left_section_item(index) !== null
              ? left_section_item(index).location
              : ""
          }}
        </div>
        <div class="description" v-if="left_section_item(index) !== null">
          <div
            class="item"
            v-for="(item, i) in left_section_item(index).description"
            :key="i"
          >
            <img src="@/assets/images/arrow_right.svg" alt="" class="arrow" />
            {{ item }}
          </div>
        </div>
        <div class="button_section" v-if="left_section_item(index) !== null">
          <apply-button
            :active="true"
            @onClick="onClickApply(left_section_item(index))"
          />
        </div>
      </div>
      <div class="right_section">
        <div class="title">
          {{
            right_section_item(index) !== null
              ? left_section_item(index).title
              : ""
          }}
        </div>
        <div class="location">
          {{
            right_section_item(index) !== null
              ? left_section_item(index).location
              : ""
          }}
        </div>
        <div class="description" v-if="right_section_item(index) !== null">
          <div
            class="item"
            v-for="(item, i) in right_section_item(index).description"
            :key="i"
          >
            <img src="@/assets/images/arrow_right.svg" alt="" class="arrow" />
            {{ item }}
          </div>
        </div>
        <div class="button_section" v-if="right_section_item(index) !== null">
          <apply-button
            :active="true"
            @onClick="onClickApply(right_section_item(index))"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ApplyButton from "@/components/ApplyButton";
export default {
  name: "CurrentPositionsSection",
  components: {
    "apply-button": ApplyButton
  },
  data() {
    return {
      // get all the jobs
      jobs: [
        {
          title: "Software Engineer - Full Stack",
          location: "Bangalore / Remote",
          description: [
            "Our back-end is written in Node.js",
            "Our front-end is Vue.js",
            "We use MongoDB and PostgreSQL databases",
            "We use RabbitMQ as message broker and Redis for caching",
            "Familiarity with Docker and Kubernetes is a huge plus",
            "At a minimum, you should be proficient in Javascript",
            "You will need to be a fast and eager learner"
          ]
        },
        {
          title: "Intern - Digital Marketing",
          location: "Bangalore / Remote",
          description: [
            "We love out-of-the-box ideas for guerrilla marketing",
            "We also create and run ads on the social media",
            "Our approach is to measure everything and improve",
            "Being super-active on social media is a big plus",
            "You should have flawless command over the English language",
            "Ability to work with data in spreadsheets will help you",
            "You will need to have a can-do-will-do attitude"
          ]
        },
        {
          title: "Intern - Software Engineering",
          location: "Bangalore / Remote",
          description: [
            "Our back-end is written in Node.js",
            "Our front-end is Vue.js",
            "We use MongoDB and PostgreSQL databases",
            "We use RabbitMQ as message broker and Redis for caching",
            "We use Docker and Kubernetes for deployment",
            "You should be comfortable coding in Javascript",
            "You will need to be a fast and eager learner"
          ]
        }
      ]
    };
  },
  computed: {
    number_of_flow_section() {
      // return how many flow sections needed. ie, number of jobs divided by 2
      return Math.ceil(this.jobs.length / 2);
    }
  },
  methods: {
    left_section_item(row_index) {
      // return first element of a job row based on current row index
      let index = (row_index - 1) * 2;
      // check if the index is valid
      if (this.jobs[index] !== undefined) {
        return this.jobs[index];
      } else {
        return null;
      }
    },
    right_section_item(row_index) {
      // get index of second element in a row based on the current row index
      let index = (row_index - 1) * 2 + 1;
      // check if the index is valid
      if (this.jobs[index] !== undefined) {
        return this.jobs[index];
      } else {
        return null;
      }
    },
    onClickApply(item) {
      console.log(item.title);
    }
  }
};
</script>

